.background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: -100%;
  bottom: 0;
  left: 100%;
  background-color: #f6f6f7;
  transform: rotate(180deg);
  transform-origin: 0 0;
}
.div {
  right: -28.57%;
  left: 100%;
  background-color: var(--color-gainsboro);
}
.div,
.div1,
.div2 {
  position: absolute;
  height: 100%;
  width: 28.57%;
  top: 0;
  bottom: 0;
  transform: rotate(180deg);
  transform-origin: 0 0;
}
.div1 {
  right: 7.14%;
  left: 64.29%;
  background-color: var(--color-gainsboro);
}
.div2 {
  right: 42.86%;
  left: 28.57%;
  background-color: #949498;
}
.image-icon,
.slider {
  position: absolute;
}
.slider {
  height: 0.49%;
  width: 37.33%;
  top: 92.98%;
  right: 53.6%;
  bottom: 6.53%;
  left: 9.07%;
}
.image-icon {
  height: 57.39%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 42.61%;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.panel {
  position: absolute;
  height: 39.29%;
  width: 85.33%;
  top: 47.78%;
  right: -70.67%;
  bottom: 12.93%;
  left: 85.33%;
  background-color: var(--white);
  transform: rotate(180deg);
  transform-origin: 0 0;
}
.rectangle {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-lightskyblue);
}
.bg,
.rectangle,
.text {
  position: absolute;
}
.bg {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.text {
  top: calc(50% - 11px);
  left: calc(50% - 122.5px);
  letter-spacing: 0.06px;
  line-height: 22px;
  font-weight: 600;
}
.button,
.iconarrowsright-arrow {
  position: absolute;
  overflow: hidden;
}
.iconarrowsright-arrow {
  height: 24%;
  width: 4.3%;
  top: 38%;
  right: 6.67%;
  bottom: 38%;
  left: 89.03%;
  max-width: 100%;
  max-height: 100%;
}
.button {
  height: 6.16%;
  width: 85.07%;
  top: 81.28%;
  right: 14.93%;
  bottom: 12.56%;
  left: 0;
  text-align: center;
  font-family: var(--font-poppins);
}
.discover-great-trips {
  position: absolute;
  width: calc(100% - 89px);
  top: 421px;
  left: 22px;
  font-size: var(--font-size-17xl);
  line-height: 45px;
  color: var(--color-gray-100);
  display: inline-block;
  height: 101px;
}
.search-and-book {
  position: absolute;
  width: 69.87%;
  top: calc(50% + 121px);
  left: 5.87%;
  letter-spacing: -0.24px;
  line-height: 20px;
  color: #8a8a8f;
  display: inline-block;
}
.onboarding {
  position: relative;
  background-color: var(--white);
  width: 100%;
  height: 812px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-mini);
  color: var(--white);
  font-family: var(--font-inter);
}
