  position: absolute;
  inset: 0 -100% 0 100%;
  transform: rotate(180deg);
}

.div {
  background-color: var(--color-gainsboro);
  left: 100%;
  right: -28.57%;
}

.div, .div1, .div2 {
  height: 100%;
  width: 28.57%;
